import { Component, OnInit } from '@angular/core';
import {PRIVILEGES} from '../services/constants.service';
import {ControllerService} from '../services/controller.service';
import {AlertService} from '../services/alert.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-admin-add-product',
  templateUrl: './admin-add-product.page.html',
  styleUrls: ['./admin-add-product.page.scss'],
})
export class AdminAddProductPage implements OnInit {

  form = {
    name: '',
    price: '',
    category: '',
    quantity: '',
  };

  constructor(private api: ControllerService, private toast: AlertService, private router: Router) { }

  ngOnInit() {
    const user = this.api.getCurrentUser();
    if (user !== PRIVILEGES.ADMIN) {
      this.router.navigateByUrl('/home');
    }
  }

  add() {
    if (this.api.addProduct(this.form)) {
      this.toast.show('Product Created Successfully.')
        .then(() => {
          this.form = {
            name: '',
            price: '',
            category: '',
            quantity: '',
          };
          this.router.navigateByUrl('/admin-products');
        });
    } else {
      this.toast.show('Error Creating Product');
    }
  }

  logout() {
    this.api.logout(PRIVILEGES.ADMIN);
    location.assign('/home');
  }
}
